import Divider from 'components/Divider/Divider';
import Fallback from '../../assets/images/fallback.jpg';
import {
  Card,
  TitleContainer,
  Item,
  List,
  TextContainer,
  Img,
  BtnLearnMore,
  ContainerImg,
  FavoriteIcon,
  FavoriteIconCheck,
} from './CatalogItem.styled';
import { useDispatch, useSelector } from 'react-redux';
import { favoritesCars } from '../../redux/slice';
import { selectFavorites } from '../../redux/select';

export default function CatalogItem({ list }) {
  const favorites = useSelector(selectFavorites);

  const dispatch = useDispatch();
  const {
    make,
    model,
    year,
    img,
    rentalPrice,
    address,
    rentalCompany,
    type,
    mileage,
    functionalities,
    id,
  } = list;
  const isFavorite = favorites.includes(id);


  const handleFavorite = () => {
    dispatch(favoritesCars(id));
  };
console.log()
  return (
    <Card>
      <ContainerImg>
        <Img src={img ? img : Fallback} alt="car" loading="lazy" />
      </ContainerImg>
      {isFavorite ? <FavoriteIconCheck onClick={handleFavorite}/> : <FavoriteIcon onClick={handleFavorite}/>}
      <List>
        <TitleContainer>
          <li>
            {make}, {year}
          </li>
          <li>{rentalPrice}</li>
        </TitleContainer>
        <TextContainer>
          <Item>
            {address.split(',').slice(-2)} <Divider /> {rentalCompany}
          </Item>
          <Item>
            {type} <Divider /> {model} <Divider /> {mileage} <Divider />{' '}
            {functionalities[0]}
          </Item>
        </TextContainer>
      </List>
      <BtnLearnMore type="button">Learn More</BtnLearnMore>
    </Card>
  );
}
